<template>
	<div class="body">
		<div class="body_header">
			<i class="icon iconfont icon-gongsixiangqing1"></i>
			<span>项目详情</span>
		</div>
		<div class="company_name flex_start company_name_border">
			<div class="company_name_left">
				<i class="icon iconfont icon-gongsixiangqing"></i>
				<span>{{subprojectName}}</span>
			</div>
			<div @click="changeEditStatus" class="company_name_right cursor flex_start" v-if="!isEditor">
				<span>编辑</span>
				<i class="icon iconfont icon-bianji"></i>
			</div>
		</div>
		<div class="form_box">
			<div class="form flex_between form_bottom">
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目名称
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="commitData.name" clearable :disabled="!isEditor">
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目企业名称
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="commitData.company_name" clearable :disabled="!isEditor">
						</el-input>
					</div>
				</div>
				<!-- <div class="form_list flex_start">
					<div class="form_list_left">
						币种
					</div>
					<div class="form_list_right">
						<el-select v-model="commitData.unit" clearable placeholder="请选择" :disabled="!isEditor">
							<el-option v-for="item in moneyTypeList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div> -->
				<!-- <div class="form_list flex_start">
					<div class="form_list_left">
						认购子基金名称
					</div>
					<div class="form_list_right">
						<el-select v-model="commitData.subscribe_fund_ids" clearable filterable multiple placeholder="请选择" class="subFundProjectAddSelect" :disabled="!isEditor">
							<el-option v-for="item in subscribeFundList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div> -->
				<div class="form_list flex_start">
					<div class="form_list_left">
						行业
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="commitData.industry" clearable :disabled="!isEditor">
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						数据创建人
					</div>
					<div class="form_list_right">
						<el-select v-model="commitData.create_user_id" clearable  filterable placeholder="请选择" :disabled="!isEditor">
							<el-option v-for="item in creatorList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						统一社会信用代码
					</div>
					<div class="form_list_right">
						<el-input placeholder="" v-model="commitData.credit_code" clearable disabled>
						</el-input>
					</div>
				</div>
				<!-- <div class="form_list flex_start">
					<div class="form_list_left">
						实缴（万元）
					</div>
					<div class="form_list_right">
						<el-input placeholder="" v-model="commitData.payed" clearable disabled>
						</el-input>
					</div>
				</div> -->
				<div class="form_list flex_start">
					<div class="form_list_left">
						是否舆情监测
					</div>
					<div class="form_list_right flex_start">
						<div @click="changeHideStatus(1)" class="radio_btn cursor" :class="[commitData.is_monitor?'radio_btn_active':'']">
							是
						</div>
						<div @click="changeHideStatus(2)" class="radio_btn cursor" :class="[!commitData.is_monitor?'radio_btn_active':'']">
							否
						</div>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目简介
					</div>
					<div class="form_list_right form_list_right_textarea">
						<el-input placeholder="请填写"  type="textarea" :rows="3" v-model="commitData.introduce" :disabled="!isEditor">
						</el-input>
					</div>
				</div>
				
			</div>

			<!-- 列表数据 -->
			<div class="form flex_between form_bottom" v-for="(item,index) of commitData.quote_info" :key="index">
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目企业股票代码
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="item.share_code" clearable :disabled="!isEditor">
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						上市/挂牌板块
					</div>
					<div class="form_list_right">
						<el-select v-model="item.quote_id" clearable filterable placeholder="请选择" :disabled="!isEditor">
							<el-option v-for="item in plateList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						上市时间
					</div>
					<div class="form_list_right">
						<el-date-picker v-model="item.quoted_date" type="date" value-format="yyyy-MM-dd" placeholder="选择日期" :disabled="!isEditor" :editable='false'>
    					</el-date-picker>
					</div>
				</div>
				<div class="form_list flex_start" v-if="!isEditor&&item.share_price">
					<div class="form_list_left">
						股价
					</div>
					<div class="form_list_right">
						<el-input placeholder="" v-model="item.share_price" clearable disabled>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start" v-if="isEditor">
					<span @click="deleteData(index)" v-if="commitData.quote_info.length>1">删除</span>
					<div class="table-group-title-btn btn-blue-border" @click="addData()" v-if="index==commitData.quote_info.length-1" >
						 &nbsp;&nbsp;新增数据&nbsp;&nbsp;
					</div>
				</div>
			</div>
		</div>
		<!--表单按钮模块-->
		<div class="form_submit flex_center" v-if="isEditor">
			<button @click="toCancle()">取消</button>
			<button @click="toSave()">保存</button>
		</div>

		<!--公司工商信息模块开始-->
		<div class="company_background">
			<div class="block_title flex_between">
				<div class="title_left flex_start">
					<div class="radio"> </div>
					<div class="block_name">
						合伙企业信息
					</div>
				</div>
				<div class="title_right flex_end">
					<!-- <div class="download cursor flex_center">
						<span class="iconfont icon-daoru cursor"></span>
						<span>导出企业信息</span>
					</div> -->
					<div class="download blue_btn cursor flex_center" @click="downLoadCompanyInfo">
						<span class="iconfont icon-daoru cursor"></span>
						<span>导出企业信息</span>
					</div>
				</div>
			</div>
			<company-detail-background v-bind:type="type"></company-detail-background>
		</div>
	</div>
</template>

<script>
	import companyDetailBackground from '../../pages/companyBackground/companyDetailBackground.vue'
	import {getUserlistApi} from '../../config/api'
	
	import {getChildData,getQuotedPlace,getSubFundDetail,saveFundInfo} from '../../config/apiSj'
	import { dialogConfirm } from '../../config/mUtils';
	import url from '../../config/url'
	export default {
		data() {
			return {
				subprojectName:'',//当前项目的名称
				subprojectId:'',//当前项目的ID；
				subscribeFundList: [],//认购子基金列表
				plateList:[],//上市/挂牌板块列表
				creatorList:[],//项目经理列表
				moneyTypeList:[
					{
						id:1,
						name:'人民币'		
					},
					{
						id:2,
						name:'美元'		
					},
				],//币种列表
				
				commitData: {
					quote_info:[],
				},
				isEditor:false,
				saveEditorData:{},

				type:7//舆情固定参数7
			}
		},
		created() {
			this.subprojectId = this.$route.query.id;
			// this.subprojectName = this.$route.query.name;
			this.getModuleInfo();
			// this.getFundList();
			this.getAllUserList();
			this.toGetSubFundDetail();
			
		},
		mounted() {},
		components: {
			companyDetailBackground
		},
		methods: {
			//是否显示或隐藏
			changeHideStatus(e){
				if(this.isEditor){
					if (e == 1) {
						this.commitData.is_monitor = 1;
					} else{
						this.commitData.is_monitor = 0;
					}
				}
			},

			addData(){
				this.commitData.quote_info.push({
					share_code:'',
					quoted_date:'',
					quote_id:'',
				});
			},
			deleteData(index){
				this.commitData.quote_info.splice(index,1);
			},
			changeEditStatus(){
				this.isEditor = true;
				this.saveEditorData = JSON.parse(JSON.stringify(this.commitData));
			},
			toCancle(){
				this.isEditor = false;
				this.commitData = JSON.parse(JSON.stringify(this.saveEditorData));
			},
			toSave(){
				// console.log(this.commitData);
				let commitData = JSON.parse(JSON.stringify(this.commitData));
				//处理认购子基金
				// if(commitData.subscribe_fund_ids.length){
				// 	commitData.subscribe_fund_ids = commitData.subscribe_fund_ids.join('|');
				// }else{
				// 	dialogConfirm('info','认购子基金不能为空!')
				// 	return false;
				// }
				//处理挂牌信息
				let new_quote_info = [];
				commitData.quote_info.forEach(element => {
					let newObj = {
						share_code:element.share_code,
						quoted_date:element.quoted_date,
						list_plate_id:element.quote_id,
					}
					new_quote_info.push(newObj);
				});

				let params = {
					id:this.subprojectId,
					name:commitData.name,
					company_name:commitData.company_name,
					// unit:commitData.unit,
					industry:commitData.industry,
					introduce:commitData.introduce,
					create_user_id:commitData.create_user_id,
					// subscribe_fund_ids:commitData.subscribe_fund_ids,
					quote_infos:JSON.stringify(new_quote_info),
					tianyan_id:'',
					is_monitor:commitData.is_monitor
				}
				// console.log(params);
				// return false;
				saveFundInfo(params).then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res.data);
						dialogConfirm('success','保存成功')
						this.isEditor = false;
						this.toGetSubFundDetail();
					}else{
						dialogConfirm('info',res.data.message);
					}
				})
			},

			
			//获取上市/挂牌模板信息
			getModuleInfo(){
				//请求接口
				getQuotedPlace({
					keyword:'',
				}).then(res => {
					// console.log(res);
					if (res.data.msgNo == 1) {
						// dialogConfirm('success', res.data.message);
						// console.log(res.data)
						let data = res.data.payload ;
						this.plateList = data;
					}else{
						// dialogConfirm('info', res.data.message);
					}
				})
			},

			//获取认购子基金列表
			getFundList(){
				//请求接口
				getChildData().then(res => {
					// console.log(res);
					if (res.data.msgNo == 1) {
						// dialogConfirm('success', res.data.message);
						// console.log(res.data)
						let data = res.data.payload ;
						this.subscribeFundList = data;
					}else{
						// dialogConfirm('info', res.data.message);
					}
					
				})
			},

			// 获取数据创建人列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.creatorList = res.data.payload.data;
					}
				})
			},

			//获取当前子基金项目信息
			toGetSubFundDetail(){
				getSubFundDetail({
					id:this.subprojectId,
				}).then(res => {
					// console.log(res.data);
					if(res.data.msgNo == 1) {
						this.commitData = res.data.payload;
						if(!this.commitDatacreate_user_id){
							this.commitDatacreate_user_id = ''
						}
						this.commitData.quote_info.forEach(element => {
							if(!element.quote_id){
								element.quote_id = '';
							}
						});
						this.subprojectName = res.data.payload.name;
					}
				})
			},

			//导出企业信息
			downLoadCompanyInfo(){
				var key = window.localStorage.getItem("htcx_server_key");
				let href = url.sourceDataUrl + "/web/Company/CompanyInfoExcel?company_id=" + this.subprojectId + '&key=' + key + '&new_type=' + this.type;
				// console.log(href)
				window.location.href = href;
			},
		},
	}
</script>

<style lang="scss" scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.company_name {
		padding-bottom: 20px;
	}
	.company_name_border{
		border-bottom: 1px solid #EAEAEA;
	}
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	.form_box{
		// padding: 30px 0;
	}
	.form{
		flex-wrap: wrap;
		color: #6F7071;
		padding: 30px 0 10px;
	}
	.form_bottom{
		border-bottom:1px solid #E5E5EA;
	}
	.form_list{
		display: inline-flex;
		margin-bottom: 20px;
		&>span{
			font-size: 14px;
			color: #6292F4;
			cursor: pointer;
		}
		&>button{
			width: 105px;
			height: 40px;
			border-radius: 20px;
			background: #6292F4;
			color: white;
			font-size: 14px;
			cursor: pointer;
			margin-left: 46px;
		}
	}
	.form_list_left{
		width: 130px;
		color: #6F7071;
	}
	.form_list_right{
		width: 260px;
    	// height: 40px;
	}
	.form_list_right_textarea{
		width: 800px;
	}
	.radio_btn{
		width: 73px;
		height: 40px;
		margin-right: 38px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
	}
	.radio_btn_active{
		color: white;
		background: #6292F4 !important;
	}
	.form_submit{
		margin-top: 40px;
	}
	.form_submit button{
		width: 94px;
		height: 36px;
		color: #6292F4;
		font-size: 14px;
		margin-right: 26px;
		border-radius: 20px;
		border: 1px solid #6292F4;
		
	}
	.form_submit button:nth-child(2){
		color: white;
		background: #6292F4;
	}

	.company_name_right {
		margin-left: 80px;
		color: #6292F4;
	}
	
	.company_name_right i,.company_name_right span {
		color: #6292F4;
	}
	.company_name_right i{
		margin-left: 8px;
	}


	/*工商信息等公司背景*/
	.company_background {
		margin-top: 60px;
	}
	.company_info_edit_btn{
		margin-top: 70px;
	}
	.company_info_edit_btn button{
		width: 94px;
		height: 36px;
		color: white;
		border-radius: 20px;
		background: #6292F4;
	}
	.company_info_edit_btn button:nth-child(1){
		margin-right: 28px;
	}
	
	.block_title {
		margin-bottom: 25px;
		padding-bottom: 10px;
		border-bottom: 1px solid #CECBC9;
	}
	
	.title_left>div {
		color: #6292F4;
		font-size: 16px;
	}
	
	.title_left .radio {
		width: 7px;
		height: 7px;
		margin-right: 12px;
		border-radius: 50%;
		background: #6292F4;
	}
	
	.title_right>div {
		color: #6292F4;
	}
	
	.title_right i {
		color: #6292F4;
	}
	
	.download {
		width: 160px;
		height: 30px;
		// margin-right: 35px;
		border: 1px solid #6292F4;
		border-radius: 20px;
	}
	
	.download span {
		color: #6292F4;
	}
	
	.download .iconfont {
		margin-right: 7px;
	}
	
	
	
</style>


